body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3975A9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.window-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.window-title-text {
  padding-right: 20px;
}

.window-title-buttons {
  margin-right: -6px;
  display: flex;
  justify-content: space-between;
}
.small-icon {
  padding-right: 5px;
  height: 20px;
}

.footer-buttons {
  width: 50%;
}

.footer-cut-out {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  width: 600px;
}

@media only screen and (max-width: 400px)  {
  .footer-cut-out {
    width: 310px;
  }

  .footer-buttons {
    width: 100%;
  }
}

.popup-window-container {
  position: absolute;
  z-index: 4;
  padding: 10px;

  margin-left: -15px;
  margin-top: -25px;
}

.popup-movable-header {
  cursor: -webkit-grab;
  cursor: grab;
}

.popup-movable-header:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.about-window {
  width: 570px;
}

.text-container {
  background: #fffee8;
  padding: 10px;

  margin-bottom: 15px;

  border: 1px solid;
  padding: 10px;
  box-shadow: 10px 10px #707070;
  font-size: 18px;
}

.avatar-picture {
  height: 210px;
  float: right;
  padding: 5px;
  margin-left: 2px;
}

@media only screen and (max-width: 400px)  {
  .about-window {
    width: 280px;
  }
}
.links-window {
  width: 570px;
}

.link-list {
  overflow: auto;
  overflow-y: hidden;
  color: white;
  background-color: #3968C2;
  font-weight: bold;

  font-family: Lucida Console,Lucida Sans Typewriter,monaco,Bitstream Vera Sans Mono,monospace;
}

.link-list a {
  color: yellow;
}

.link-list a:hover {
  color: red;
}

.link-list a:visited {
  color: lightgray;
}

.link-style {
  padding: 3px;
}

.globe-container {
  background-color: black;
  width: 80px;
  height: 80px;
  float: right;

  margin-left: 15px;
  margin-bottom: 15px;
}

.globe-picture {
  height: 70px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@media only screen and (max-width: 400px)  {
  .links-window {
    width: 280px;
  }
}
.projects-window {
  width: 580px;
}

.cutout-area {
